import { fold } from "../.fable/fable-library.3.1.15/Seq.js";
import { isUpper } from "../.fable/fable-library.3.1.15/Char.js";
import { join, printf, toText } from "../.fable/fable-library.3.1.15/String.js";
import { map } from "../.fable/fable-library.3.1.15/List.js";
import { compare } from "../.fable/fable-library.3.1.15/Util.js";

export function Helpers_toLowerAndCombine(separator, value) {
    return fold((acc, element) => {
        if (isUpper(element) ? (acc.length === 0) : false) {
            return acc + element.toLocaleLowerCase();
        }
        else if (isUpper(element) ? (acc.length !== 0) : false) {
            const arg30 = element.toLocaleLowerCase();
            return toText(printf("%s%s%s"))(acc)(separator)(arg30);
        }
        else {
            return acc + element;
        }
    }, "", value.split(""));
}

export function Helpers_pascalToCamelCase(value) {
    const arg20 = value.slice(1, value.length);
    const arg10 = value[0].toLocaleLowerCase();
    return toText(printf("%c%s"))(arg10)(arg20);
}

export function Helpers_pascalToKebabCase(value) {
    return Helpers_toLowerAndCombine("-", value);
}

export function Helpers_toPsuedoClass(value) {
    return toText(printf(":%s"))(value);
}

export function Helpers_toPsuedoElement(value) {
    return toText(printf("::%s"))(value);
}

export function Helpers_combineList(list, value, seperator) {
    return join(seperator, map(value, list));
}

export function Helpers_combineWs(value, list) {
    return Helpers_combineList(list, value, " ");
}

export function Helpers_combineComma(value, list) {
    return Helpers_combineList(list, value, ", ");
}

export function Helpers_clamp(min, max, value) {
    if (compare(value, max) > 0) {
        return max;
    }
    else if (compare(value, min) < 0) {
        return min;
    }
    else {
        return value;
    }
}

export function Converters_floatToPercent(f) {
    const arg10 = (~(~(f * 100))) | 0;
    return toText(printf("%d%%"))(arg10);
}

export function Color_rgb(r, g, b) {
    return toText(printf("rgb(%d, %d, %d)"))(r)(g)(b);
}

export function Color_rgba(r, g, b, a) {
    return toText(printf("rgba(%d, %d, %d, %f)"))(r)(g)(b)(a);
}

export function Color_hex(value) {
    if (value.indexOf("#") === 0) {
        return value;
    }
    else {
        return toText(printf("#%s"))(value);
    }
}

export function Color_hsl(h, s, l) {
    const arg30 = Converters_floatToPercent(l);
    const arg20 = Converters_floatToPercent(s);
    return toText(printf("hsl(%d, %s, %s)"))(h)(arg20)(arg30);
}

export function Color_hsla(h, s, l, a) {
    const arg40 = Converters_floatToPercent(a);
    const arg30 = Converters_floatToPercent(l);
    const arg20 = Converters_floatToPercent(s);
    return toText(printf("hsla(%d, %s, %s, %s)"))(h)(arg20)(arg30)(arg40);
}

