import { Union } from "../../.fable/fable-library.3.1.15/Types.js";
import { string_type, union_type } from "../../.fable/fable-library.3.1.15/Reflection.js";

export class Format extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Truetype", "Opentype", "EmpbeddedOpentype", "Woff", "Woff2", "Svg"];
    }
}

export function Format$reflection() {
    return union_type("Fss.FssTypes.FontFace.Format", [], Format, () => [[], [], [], [], [], []]);
}

export class Source extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Url", "UrlFormat", "Local"];
    }
}

export function Source$reflection() {
    return union_type("Fss.FssTypes.FontFace.Source", [], Source, () => [[["Item", string_type]], [["Item1", string_type], ["Item2", Format$reflection()]], [["Item", string_type]]]);
}

