import { Union } from "../../.fable/fable-library.3.1.15/Types.js";
import { getUnionFields, name, union_type } from "../../.fable/fable-library.3.1.15/Reflection.js";
import { masterTypeHelpers_keywordsToString, Keywords } from "./MasterTypes.js";

export class Visibility_Visibility extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Visible", "Hidden", "Collapse"];
    }
}

export function Visibility_Visibility$reflection() {
    return union_type("Fss.FssTypes.Visibility.Visibility", [], Visibility_Visibility, () => [[], [], []]);
}

export class Visibility_PaintOrder extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Stroke", "Markers", "Fill"];
    }
}

export function Visibility_PaintOrder$reflection() {
    return union_type("Fss.FssTypes.Visibility.PaintOrder", [], Visibility_PaintOrder, () => [[], [], []]);
}

export class Visibility_BackfaceVisibility extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Visible", "Hidden"];
    }
}

export function Visibility_BackfaceVisibility$reflection() {
    return union_type("Fss.FssTypes.Visibility.BackfaceVisibility", [], Visibility_BackfaceVisibility, () => [[], []]);
}

export function visibilityHelpers_visibilityToString(visibility) {
    if (visibility instanceof Visibility_Visibility) {
        return name(getUnionFields(visibility, Visibility_Visibility$reflection())[0]).toLocaleLowerCase();
    }
    else if (visibility instanceof Keywords) {
        return masterTypeHelpers_keywordsToString(visibility);
    }
    else {
        return "Unknown visibility";
    }
}

