export function unitHelpers_lengthPercentageToString(v) {
    if (v instanceof Length) {
        return unitHelpers_sizeToString(v);
    }
    else if (v instanceof Percent) {
        return unitHelpers_percentToString(v);
    }
    else {
        return "Unknown length/percentage";
    }
}

