import { Union } from "../../.fable/fable-library.3.1.15/Types.js";
import { union_type } from "../../.fable/fable-library.3.1.15/Reflection.js";
import { masterTypeHelpers_IntToString, CssInt } from "./MasterTypes.js";

export class Animation_Direction extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Reverse", "Alternate", "AlternateReverse"];
    }
}

export function Animation_Direction$reflection() {
    return union_type("Fss.FssTypes.Animation.Direction", [], Animation_Direction, () => [[], [], []]);
}

export class Animation_FillMode extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Forwards", "Backwards", "Both"];
    }
}

export function Animation_FillMode$reflection() {
    return union_type("Fss.FssTypes.Animation.FillMode", [], Animation_FillMode, () => [[], [], []]);
}

export class Animation_IterationCount extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Infinite"];
    }
}

export function Animation_IterationCount$reflection() {
    return union_type("Fss.FssTypes.Animation.IterationCount", [], Animation_IterationCount, () => [[]]);
}

export class Animation_PlayState extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Running", "Paused"];
    }
}

export function Animation_PlayState$reflection() {
    return union_type("Fss.FssTypes.Animation.PlayState", [], Animation_PlayState, () => [[], []]);
}

export function animationHelpers_iterationCountToString(iterationCount) {
    if (iterationCount instanceof Animation_IterationCount) {
        return "infinite";
    }
    else if (iterationCount instanceof CssInt) {
        return masterTypeHelpers_IntToString(iterationCount);
    }
    else {
        return "Unknown animation iteration count";
    }
}

